#cfw_welcome_notice {
    padding: 24px;

    h2 {
        margin-top: 0;
    }
}

body.cfw-admin-page {
    .form-table td {
        position: relative;
    }
}

.cfw-license-upgrade-blocker-og ~ * {
    display: none !important;
}

.cfw-license-upgrade-blocker-og {
    .inner {
        position: relative;
        padding: 2rem;
        text-align: center;

        h3 {
            margin-top: 0;
        }
    }
}

body.post-type-cfw_order_bumps #wpbody {
    height: 100vh;
}

body.post-type-cfw_order_bumps .cfw-license-upgrade-blocker-og {
    position: absolute;
    top: 0;
    left: -20px;
    right: 0;
    bottom: 0;
    box-shadow: none;

    .inner {
        top: 50%;
        transform: translateY(-100%);
    }
}

body.cfw-admin-page {
    .notice, div.updated, div.error {
        &:not([id^="pressmodo-notice-cfw_"]):not(.inline) {
            display: none !important; // hide until page load
        }
    }
}

div[id^="pressmodo-notice-"] {
    display: none !important; // hide until page load
}

.cfw-admin-notices-container {
    margin-bottom: -20px;
    clear: both; // fix issue with screen options / help widget

    div[id^=pressmodo-notice-] {
        display: flex !important;
    }

    .notice, div.updated, div.error {
        margin-bottom: 15px;
        margin-inline-start: 40px;
        margin-inline-end: 40px;


        &:nth-child(2) {
            margin-top: 20px; // pad the top of the first one
        }
    }
}

// Hide duplicate notices in .wrap when editing posts in specific post types
// This ensures notices are only displayed in .cfw-admin-notices-container (outside .wrap)
// and prevents duplicate notices from appearing. Inline notices are always visible.
body.post-type-cfw_ab_test,
body.post-type-cfw_pickup_location,
body.post-type-cfw_acr_emails {
    #wpbody-content > .wrap {
        .notice:not(.inline),
        div.updated:not(.inline),
        div.error:not(.inline) {
            display: none !important;
        }
    }
}