// Tailwind Forms @apply styles for .cfw-tw container
// Since @tailwindcss/forms uses strategy: 'class', we need to apply form classes
// This file applies them automatically to all form elements within .cfw-tw

.cfw-tw {
    // Text-like inputs
    input[type="text"],
    input[type="email"],
    input[type="password"],
    input[type="number"],
    input[type="url"],
    input[type="date"],
    input[type="datetime-local"],
    input[type="month"],
    input[type="week"],
    input[type="time"],
    input[type="search"],
    input[type="tel"] {
        @apply form-input;
        @apply mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-300 focus:ring focus:ring-blue-200 focus:ring-opacity-50;
    }

    // Textareas
    textarea {
        @apply form-textarea;
        @apply mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-300 focus:ring focus:ring-blue-200 focus:ring-opacity-50;
    }

    // Select dropdowns
    select {
        @apply form-select;
        @apply block w-full mt-1 rounded-md border-gray-300 shadow-sm focus:border-blue-300 focus:ring focus:ring-blue-200 focus:ring-opacity-50;
    }

    // Checkboxes
    input[type="checkbox"] {
        @apply form-checkbox;
        @apply rounded border-gray-300 text-blue-600 shadow-sm focus:border-blue-300 focus:ring focus:ring-offset-0 focus:ring-blue-200 focus:ring-opacity-50;
    }

    // Radio buttons
    input[type="radio"] {
        @apply form-radio;
    }
}
